import Papa from 'papaparse'
async function fetchTextFile (file) {
  try {
    const response = await fetch(file)
    if (!response.ok) {
      throw new Error('Problém s načítaním receptov', file)
    }
    const text = await response.text()
    const lines = text.split('#')
    const regex = /<(.*?)>/g // string in <>
    const images = lines.map((element) => { // create new array 'images'
      const matches = element.match(regex) // find <> in every element of array
      return matches ? matches.map((match) => match.slice(1, -1)) : [] // populate array 'matches' with images, match.slice removes <> symbols
    })
    const objects = lines.map((line, index) => ({ // array of objects
      line: line.replace(regex, ''), // remove image name from recipe
      image: images[index][0] // there is array of arrays, therefore image has to be first element of the "inside" array [0]
    }))
    // console.log('fetchTextFile()')
    // console.log(images)
    // console.log(lines)
    // console.log(objects[2])
    return objects
  } catch (error) {
    console.error('Error fetching the text file:', error)
    return null
  }
}
async function buttons (file) {
  return new Promise((resolve) => {
  Papa.parse(file, {
  download: true,
  header: true,
  skipEmptyLines: true,
  complete: function(results)
  {
    return resolve(results.data)
  },
  })
})
}
export const receptyG = await fetchTextFile('receptyG.txt')
export const receptyP = fetchTextFile('receptyP.txt')
export const buttonsgreen = await buttons('buttonsg.csv')
export const buttonspurple = await buttons('buttonsp.csv')
