<template>
<br>
<div v-show="home">
  <div id="center">
    <q-btn v-for="button in buttonsgreen" :key="button.id" :label="button.label" @click="rec=true, home=false, green=true, click(button.id)" no-caps style="width: 100px;" color="secondary"/>
    
    <q-btn v-for="button in buttonspurple" :key="button.id" :label="button.label" @click="rec=true, home=false, green=false, click(button.id)" no-caps style="width: 100px;" color="purple"/>
    
  </div>
</div>
<div v-show="rec">
  <div class="text-center">
        <q-btn no-caps style="width: 100px; margin: 5px;" color="secondary" label="Domov" @click="rec=false, home=true"/>
      </div>
        <div style="padding-left: 30px; padding-right: 30px;  text-align: justify; font-size: 17px;">
          <div id="just-line-break">
            {{ recept[id].line }}
          </div>
        </div>
      <div v-if="image">
        <img style="margin-left: 30px;" :src="image" />
      </div>
</div>
</template>

<script>
import { receptyG, receptyP, buttonsgreen, buttonspurple } from '../components/read.js'

export default {
  data () {
    return {
      buttonsgreen,
      buttonspurple,
      recept: [{
        line: '',
        image: ''
      }],
      image: '',
      line: '',
      id: 0,
      home: true,
      rec: false,
      green: true
    }
  },
  
  methods: {
    click (id) {
      if (this.green) {
        const result = receptyG
          this.recept = result
          // console.log(result)
          this.image = result[id].image
          this.id = id // id is used in html line {{ recept[id].line }}
        
      } else {
        receptyP.then(result => {
          this.recept = result
          this.image = result[id].image
          this.id = id
        }
        )
      }
    }
  }
}
</script>
<style>
#just-line-break {
  white-space: pre-line;
}
img {
    width:85%;
    height:85%;
    }
#center {
  display: grid;
  grid-template-columns: repeat(auto-fit, 100px);
  gap: 5px 5px;
  justify-content: center;
}
</style>
